import { TImage, TLink } from "@/types";
import Link from "next/link";
import { StrapiImage } from "../../ui/strapi-image";
import { Button } from "../../ui/button";


export interface IHeroSectionProps {
  id: number;
  documentId: string;
  __component: string;
  heading: string;
  subHeading: string;
  description: string;
  image: TImage;
  link: TLink[];
  onHomepage: boolean;
}

export function HeroSection({ data }: { data: IHeroSectionProps }) {
    if (!data) return null;

    const { heading, subHeading, description, image, link, onHomepage } = data;

  console.dir(data, { depth: null });

  // Page variant for internal pages
  if (!onHomepage) {
    return (
      <section className="relative h-[50vh] md:h-[60vh] overflow-hidden">
        <StrapiImage
          alt={image.alternativeText ?? "no alternative text"}
          className="absolute inset-0 object-cover w-full h-full"
          src={image.url}
          height={2160}
          width={3840}
        />
        <div className="relative px-8 py-4 z-10 flex flex-col items-center justify-center h-full bg-black/60 text-center">
          <h1 className="relative py-4 max-w-4xl overflow-hidden font-stretch-75% text-3xl md:text-5xl lg:text-7xl text-brand-pink font-serif">
            {heading}
          </h1>
          {subHeading && (
            <p className="mt-4 font-light text-white font-stretch-90% max-w-3xl text-base md:text-lg lg:text-xl">
              {subHeading}
            </p>
          )}
          {description && (
            <p className="mt-4 font-light text-white font-stretch-90% max-w-3xl text-base md:text-lg lg:text-xl">{description}</p>
          )}
        </div>
      </section>
    );
  }

  // Homepage variant (default)
  return (
    <section className="relative h-screen overflow-hidden">
      <StrapiImage
        alt={image.alternativeText ?? "no alternative text"}
        className="absolute inset-0 object-cover w-full h-full aspect/16:9"
        src={image.url}
        height={2160}
        width={3840}
      />
      <div className="relative px-8 py-4 z-10 flex flex-col items-center justify-center h-full bg-black/50 text-center">
        <h1 className="relative py-4 max-w-3xl overflow-hidden font-stretch-75% text-2xl md:text-4xl lg:text-6xl text-brand-more-pink font-serif">{heading}</h1>
        <p className="mt-4 font-light text-white font-stretch-90% max-w-2xl text-lg md:text-lg lg:text-xl">{subHeading}</p>
        <div className="flex flex-col md:flex-row gap-4 mt-8">
            {link.map((btn) => (
                <Link key={btn.id} href={btn.href}>
                    <Button className= "inline-flex border-[1.5px] border-brand-red italic items-center text-lift-subtle text-brand-red font-serif text-xl justify-center px-10 py-8 font-bold bg-brand-pink hover:bg-brand-pink/90" variant="secondary" >
                        {btn.label}
                    </Button>
                </Link>
            ))}
        </div>
      </div>
    </section>
  )
}